
.feature1{
  max-width: 1440px;
  height: 692px;
  width: 94%;
  display: flex;
/*   flex-wrap: wrap; */
  justify-content: space-between;
  flex-direction: row;
  margin: auto;
  padding-left: 100px ;
  padding-right: 100px ;
  padding-top: 56px;
  /* border: 1px solid green; */
}

.feature2{
  background-color:  #EFEFFB; 
}

.feature1 .image  img{
  width: 315.44px;
  height: 560px;
}

.feature1.content h1{
  font-size: 40px;
  color: #102437;
}

.feature1 .content {
  padding-top: 130px !important;  
  max-width: 650px;
  /* align-items: center; */
}
.content-padding h1{
  width: 516px;
  height: 112px;
  font-weight: 600;
font-size: 48px;
}
.reverse2{
  padding-left: 10px;
}
.feature1 .content p{
  color: #7E8091;
  padding: 6px 0;
}
.list{
  display: flex;
  max-width: 400px;
  justify-content: space-between;
}

.list .list-item li {
  align-items: center;
  display: flex;
  font-weight: 400;
  font-size: 16px;
  margin-bottom: -10px;
}

.list .list-item li i {
  font-size: 10px;
  color: #6263D5;
  margin-bottom: -2px;
  
}

.list .list-item li span {
  font-size: 16px;
  font-weight: 400;
  color: #7E8091;
  margin-left: 6px;
  
}

 



@media  (max-width: 730px) {
  .feature1 .content {
    padding-top: 0px !important;  
  }

  .feature1.content h1{
    font-size: 30px !important;
  }
  .feature1 .content-padding{
 
    margin-right: 0px !important;
   }
}