@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,400;1,500;1,600;1,700&display=swap');

*{
  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.hero-color{
  background-color: #6263D5;

}

.hero{
  padding-left: 100px; 
  padding-right: 100px; 
  max-width: 1440px;
  height: 743px;
  width: 94%;
  display: flex;
  flex-wrap: wrap;
  padding-top: 6rem;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  margin: auto;
  color:  #F2F2F2;
  padding-bottom: 2rem;
  
}
.hero .image{
  width: 315.44px;
  height: 540px;
}

.hero .content h1{
  font-size: 3.3rem;
  margin-bottom: 32px;
  margin-top: -131px;
}

.image  img{
  width: 315.44px;
  height: 540px;
margin-top: -25px ;
}

.text{
  display: flex;
  margin-top: 10px;
  align-items: flex-start;
  margin-bottom: 32px;
}
.heading{
  display: flex;
  align-items: center;
  /* margin-top: 4px; */
}

.icon i{
  font-size: 3px;
  color: black;
  padding: 5px;
  border-radius: 50px;
  background-color: #FFFBEA;  
}

.heading p  .bold-heading{
  font-size: 16px;
  font-weight: 600;
  margin-left: 5px;
}

.heading p{
  /* border: 1px solid black; */
  font-size: 15px;
}
.goodies{
  font-size: 13px;
  color: #838383;
  margin: 30px 0;
}
.heading p .cut{
  text-decoration: line-through;
  margin-left: 5px;
}
.playStoteBtn{
  display: flex;
  flex-direction: row;
  align-items: center;
  border: 1px solid transparent;
}
.playStoteBtn-second{
 margin-left: 32px;
 margin-top: -4px;
 font-weight: 400;
 font-size: 16px;
}

.playstore{
  display: flex;
  align-items: center;
  width: auto;
  transition: .3s;
}


.playstore img{
  width: 187px;
  height: 66px;
  margin-left: -10px;
  margin-top: -10px;
}

a{
  text-decoration: none;
  color: white;
}






@media  (max-width: 730px) {
  .contactUs,.footer,.hero, .faq, .glide, .bottom-text{
    flex-direction: column;
  }
  .feature1{
    flex-direction: column-reverse !important;
    align-items: center;
    text-align: center;
  }
.just-test{
  display: none;
}
  .row textarea{
    width: 340px !important;
  }
  .content{
    text-align: center;
  }
  .playStoteBtn{
    text-align: center;
    justify-content: center;
  }
.reverse{
  flex-direction: column !important;
}

    .hero .image, .feature1 .image  {
     margin-top: 2rem;
    }

 
}


@media  (max-width: 400px) {
  .list{
    flex-direction: column;
    align-items: center;
  }

  
  .list .list-item li{
    margin-bottom: 4px;
  
  }

  .btn .submitbtn{
    
    width: 100% !important;
}
}
