.contactUs,.hero,.faq, .slider{
    max-width: 1440px;
    height: 628px;
    padding-left: 100px;
    padding-right: 100px;
    width: 94%;
    margin: auto;
    font-family: 'Poppins', sans-serif;
  }
.faq-heading {
    text-align: center;
    margin: 10px 0;
    padding-bottom: 2rem;
    padding-top: 58px;
    font-size: 2rem;
    font-weight: 700;
    
/* border: 1px solid yellow; */
}

.faq {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 2rem;
    padding-top: -20%;
    margin-top: -120px;
    margin-bottom: -120px;
    /* padding-left: 100px;
    padding-right: 100px; */
    /* border: 1px solid green; */

}

.qna{
    flex-basis: 65%;
}

.img img {
    width: 284px;
    height: 284px;}
.item{
    border: 1px solid #E8E8E8;
    color: #333333;
    width: 702px;
    height: 80px;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 8px;
    transition: .5s;
}

.question{
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    /* border: 1px solid red; */
    margin-top: 20px;
    margin-bottom: 10px;
    
}
.question h2{
    font-size: 20px;
    font-weight: 400;
    /* position: absolute; */
    padding: 25px auto 31px 32.2px;
width: 406.58px;
height: 24px;
left: 244.2px;
}
.question span{
    margin-left: 10px;
}

.answer{
    max-height: 0;
    overflow: hidden;
    transition: all .5s cubic-bezier(0,1,0,1);
    font-size: 14px;
    color: rgb(255, 255, 255);
}

.answer.show{
    max-height: 9999px;
    height: auto;
    transition: all .5s cubic-bezier(1,0,1,0);
    padding-bottom: 20px;

}
.answer hr{
    border: none;
    border-bottom: 1px solid white;
    margin: 10px 0;
}
.show{
    background-color: #6263d5;
    color: white;
    border: none;
height: 184px;
font-weight: 500;
font-size: 16px;
padding-bottom: 51px;
}